header .country-inp {
    display: flex;
    border-radius: 4px;
}

header .country-picker-selector input {
    background-color: #efefef;
    border: none;
    border-radius: 4px;
    outline: none;
    padding: 10px;
    width: 100%;
}

header .country-picker-selector {
    margin-left: auto;
    display: flex;
    width: 100px;
    padding-left: 12px;
}

header .country-flag-box {
    margin-right: 5px;
}

header .country-flag-box img {
    padding: 5px 5px;
}

header .country-flag-box input[type="text"] {
    display: none;
}

header .country-picker-box,
header .price-picker-box {
    position: absolute;
    width: 170px;
    right: 0;
    left: auto;
    top: 100%;
}

header .currancy-name {
    width: 60px;
}

header .price-picker-box li {
    padding: 2px 5px;
}

footer .country-picker-selector .country-inp {
    margin-top: 15px;
}

