.country-picker-box,
.price-picker-box {
    box-shadow: 0 0 10px #dedede;
    height: 250px;
    width: 100%;
    overflow: auto;
    background-color: #fff;
    position: absolute;
    bottom: 100%;
    left: 0;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    z-index: -1;
    transition: .3s;
    transform: translateY(20px);
}

.country-picker-box.active,
.price-picker-box.active {
    opacity: 1;
    visibility: visible;
    pointer-events: unset;
    transform: translateY(0px);
    z-index: 99;
}

.country-picker-item {
    color: #666;
    font-size: 11px;
    padding: 7px;
    display: flex;
    border-bottom: 1px solid #ddd;
    transition: .3s;
}

.country-picker-item:hover {
    background-color: #dad5d5;
}

.country-flag {
    flex: 0 0 auto;
    margin-right: 5px;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-size: contain;
    background-position: 50%;
    background-repeat: no-repeat;
    line-height: 16px;
}

