}

.price-picker-box li {
    margin: 0;
    padding: 0 10px;
    font-size: 13px;
    list-style: none;
}

.price-picker-box li span {
    margin-right: 5px;
    font-weight: bold;
    display: inline-block;
}

footer .price-picker-box {
    height: 210px;
}

footer .country-picker-selector input {
    background-color: #efefef;
    border: none;
    border-radius: 4px;
    outline: none;
    padding: 10px;
    width: 100%;
    color: #000;
}

@media(max-width: 767px) {
    header .country-picker-selector {
        width: 75px;
        padding-left: 6px;
    }

    .country-inp img {
        width: 30px;
    }

    header .currancy-name {
        width: 35px;
    }

    .country-picker-selector input {
        padding: 5px;
        font-size: 12px;
    }

    header .country-picker-box,
    header .price-picker-box {
        right: 0;
    }

    footer .country-picker-box,
    .price-picker-box {
        height: 200px;
    }

    footer .country-picker-selector {
        width: 80%;
    }

    .country-picker-selector input {
        background-color: #efefef;
        border: none;
        border-radius: 4px;
        outline: none;
        padding: 10px;
        width: 100%;
        color: #000;
    }

    .price-picker-box li span {
        margin-right: 5px;
        font-weight: bold;
        display: inline-block;
        position: absolute;
        left: 10px;
    }

    .price-picker-box li {
        margin: 0;
        padding: 0 0 0 35px;
        font-size: 12px;
        list-style: none;
    }
}